export const LOGIN = "user/LOGIN";
export const LOGOUT = "user/LOGOUT";
export const LOGIN_REQUESTED = "user/LOGIN_REQUESTED";
export const USER_SIGNUP = "user/USER_SIGNUP";
export const USER_EDIT = "user/USER_EDIT";
export const LOAD_PRODUCTS = "user/LOAD_PRODUCTS";



const initialState = {
  loggedIn: false,
  loggingIn:false,
  products:[]
}
export default (state = initialState, action) => {
  console.log(state);
  switch (action.type) {
    case LOGIN_REQUESTED:
      return {
        ...state,
        loggingIn: true
      }

    case LOGIN:
      return {
        ...state,
        loggedIn: !state.loggedIn,
        loggingIn: !state.loggingIn,
      }
      case LOAD_PRODUCTS:
        return {
          ...state,
          products:action.payload
        }

    default:
      return state
  }
}


export const login = () => {
  return dispatch => {
    dispatch({
      type: LOGIN_REQUESTED
    })

    dispatch({
      type: LOGIN
    })
  }
}

export const loginWithAPI = (data) => {
  return dispatch => {
    console.log(data)
    dispatch({
      type: LOGIN_REQUESTED
    })
    dispatch({
      type:LOGIN,
    })
    return setTimeout(() => {
      dispatch({
        type: LOGIN,
      })
    }, 3000)
  }
}

export const loadProducts = (data) =>{
  //console.log(data);
  return dispatch => {
    dispatch({
      type:LOAD_PRODUCTS,
      payload:data
    })
  };
}


