
import React,{Component} from 'react'
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import SuccessOutlineButton from '../button/Button';
import { Container, Row, Col, Button } from "reactstrap";
import Card from "../card/Card";
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import store from '../../store';
import {
  login,
  loginWithAPI,
  loadProducts
} from '../../modules/user'
const images = require.context('../../images',true);
let products = [
    {"title":"Hamburger","location":"Paris","image":"./dress/dress1.png","subcategory":"men","price":"200"},
    {"title":"Fries","location":"Venice","image":"./dress/dress2.png","subcategory":"women","price":"100"},
    {"title":"Coke","location":"New York","image":"./dress/dress3.png","subcategory":"men","price":50},
    {"title":"Pepsi","location":"Manchester","image":"./dress/dress1.png","subcategory":"men","price":50}
]
store.dispatch({
  type:"user/LOAD_PRODUCTS",
  payload:products
})
function SampleNextArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}
function fetchproductdata() {
    fetch('')
        .then((result) => {
            return result.json();
        }).then((jsonResult) => {
            this.setState({ "recommended": jsonResult });
            console.log(this.state.recommended);
        })
        .catch((error) => {
            console.log(error);
        });
}


let settings = {
    arrows: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [{ breakpoint: 1320, settings: { slidesToShow: 3 } },
      { breakpoint: 1024, settings: { slidesToShow: 2 } },
    { breakpoint: 767, settings: { slidesToShow: 1, arrows: false,dots: true, } }],
};
class Home extends Component{
  constructor(props){
    super(props)
  }
  componentDidMount(){
    /*let products = [
        {"title":"Hamburger","location":"Paris","image":"./dress/dress1.png","subcategory":"men","price":"200"},
        {"title":"Fries","location":"Venice","image":"./dress/dress2.png","subcategory":"women","price":"100"},
        {"title":"Coke","location":"New York","image":"./dress/dress3.png","subcategory":"men","price":50},
        {"title":"Pepsi","location":"Manchester","image":"./dress/dress1.png","subcategory":"men","price":50}
    ]
    this.props.loadProducts(products);*/
  }
  render(){
    return(
            <div className="bg-light">
            <h1>Home</h1>
            <p>
              <button onClick={this.props.login} disabled={this.props.loggingIn}>Login</button>
              <button onClick={this.props.login} disabled={this.props.loggingIn}>Login Async</button>
            </p>

            <p><button onClick={() => this.props.changePage()}>Go to about page via redux</button></p>
            <Container>
                <div className="pt-5 font-white ">
                    <h3 className="text-uppercase pl-8-5">Recommended for you</h3>
                    <p className="py-2 pl-8-5">we think you will love these picks</p>
                    <Container fluid={true}>
                            <Slider {...settings}>
                                {this.props.products && this.props.products.map((item, index) =>
                                    <Col>
                                        <Card>
                                            <img alt="product image" src={images(this.props.products[index].image,true)} className=" p-3" />
                                            <span>
                                                <h5 className="pull-left">{this.props.products[index].subcategory}</h5>
                                                <i className="fa fa-heart-o fa-2x pull-right"></i>
                                            </span>
                                            <hr className="w-75" />
                                            <p>{this.props.products[index].price}</p>
                                        </Card>
                                    </Col>
                                )}
                            </Slider>
                        <div class="d-flex justify-content-center mt-5 pb-5">
                            <Link className="button btn btn-secondary" to={'/'}>VIEW ALL</Link>
                        </div>
                    </Container>
                </div>

                <div className="pt-5 font-white">
                    <h3 className="text-uppercase pl-8-5">Home Sale</h3>
                    <p className="py-2 pl-8-5">we think you will love these picks</p>
                    <Container>
                            <Slider {...settings}>
                                {this.props.products && this.props.products.map((item, index) =>
                                    <Col>
                                        <Card>
                                            <img alt="product image" src={images(this.props.products[index].image,true)} className="img-responsive p-3" />
                                            <span>
                                                <h5 className="pull-left">{this.props.products[index].title}</h5>
                                                <i className="fa fa-heart-o fa-2x pull-right"></i>
                                            </span>
                                            <hr className="w-75" />
                                            <p>{this.props.products[index].location}</p>
                                        </Card>
                                    </Col>
                                )}
                            </Slider>
                        <div class="d-flex justify-content-center mt-5 pb-5">
                            <Link className="button btn btn-secondary"  to={'/'}>VIEW ALL</Link>
                        </div>
                </Container>
                </div>
                </Container>
            </div>
        );
      }
    }


const mapStateToProps = state => ({
  login: state.user.loggedIn,
  loggingIn: state.user.loggingIn,
  products:state.user.products
})
const mapDispatchToProps = dispatch => bindActionCreators({
  login,
  loginWithAPI,
  loadProducts,
  changePage: () => push('/about-us')
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



