import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Link} from 'react-router-dom';
import Login from '../User';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button } from 'reactstrap';

export default class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    },
  ()=>{
    console.log(this.state);
    this.forceUpdate();
  });
  }
  getLink(text,link){
    return  (
      <Link className="dropdown-item" to={link}>{text}</Link>
    )
  }
  render() {
    return (
      <div>
      <div className="d-none d-sm-block">
      <Login isOpen={this.state.isOpen} toggle={this.toggle}/>
        <Navbar dark="true" color="dark" expand="xs">
          <NavbarBrand href="/">LessForDress</NavbarBrand>
            <Nav className=" ml-auto" navbar>
              <NavItem>
                <Link className="nav-link" to="/about-us">About</Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/">Home</Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/item">Item</Link>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Options
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("My Cart","/cart")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>(<button className="dropdown-item" onClick={this.toggle}>Login</button>)}>
                    Option 2
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>this.getLink("Option3","#Option3")}>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
        </Navbar>
      </div>
      <div className="d-block d-sm-none">
        <Navbar dark="true" color="dark" expand="xs">
          <NavbarBrand href="/">LessForDress</NavbarBrand>
            <Nav className=" ml-auto" navbar>
              <NavItem>
                <Link className="nav-link" to="/about-us"><i class="fa fa-bookmark"></i></Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/"><i class="fa fa-home"></i></Link>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  <i class="fa fa-user-o"></i>
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("My Cart","/cart")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>(<button className="dropdown-item" onClick={this.toggle}>Login</button>)}>
                    Option 2
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>this.getLink("Option3","#Option3")}>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
        </Navbar>
      </div>

      </div>
    );
  }
}



